import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import { useInView } from 'react-intersection-observer';
import "../styles/aboutMe.css"; // Asegrate de tener los estilos necesarios
import photo1 from "../images/photo1.png";
import photo2 from "../images/photo2.png";
import photo3 from "../images/photo3.png";
import photo4 from "../images/photo4.png";
import photo5 from "../images/photo5.png";
import photo6 from "../images/photo6.png";
import photo7 from "../images/photo7.png";
import photo8 from "../images/photo8.png";
import photo9 from "../images/photo9.png";
import photo10 from "../images/photo10.png";
import photoPerfil from "../images/Milena Domenez.jpg";

const AboutMe = () => {
  const { ref: titleRef, inView: titleInView } = useInView({
    triggerOnce: true,
    threshold: 0.1,
  });
  
  const { ref: galleryRef, inView: galleryInView } = useInView({
    triggerOnce: true,
    threshold: 0.1,
  });

  return (
    <Container className="about-me-section">
      <Row className="align-items-center">
        <Col md={6} className={`text-section ${titleInView ? 'slide-in-left' : ''}`} ref={titleRef}>
          <h2>Un poco sobre m</h2>
          <p>
            Hola! Mi nombre es Milena Domenez, tengo 31 aos y comenc este
            hermoso camino hace 3 aos. Desde entonces fui capacitndome cada
            da para ser la profesional que soy hoy, dedicndome el 100% a mi
            trabajo, perfeccionndome siempre y adaptndome a las ltimas
            tendencias para brindarles el mejor servicio posible. Hoy Factory
            sigue creciendo, gracias a mis clientas, las que siempre me eligen y
            las que recin comienzan a acompaarme. Y si an no nos conocemos,
            no tengas dudas en contactarte conmigo, ser un placer conocerte.
            Gracias!
          </p>
          <p>
            <strong>Milena Domenez.</strong>
          </p>
        </Col>
        <Col md={6} className="image-section">
          <img
            src={photoPerfil}
            alt="Milena Domenez"
            className="profile-image"
          />
        </Col>
      </Row>
      <Row>
        <Col className={`gallery-section my-5 ${galleryInView ? 'slide-in-up' : ''}`} ref={galleryRef}>
          <h2 className="mb-5">Mis trabajos</h2>
          <div className="image-gallery">
            <img src={photo1} alt="Galera 1" className="gallery-image" />
            <img src={photo2} alt="Galera 2" className="gallery-image" />
            <img src={photo3} alt="Galera 3" className="gallery-image" />
            <img src={photo4} alt="Galera 4" className="gallery-image" />
            <img src={photo5} alt="Galera 5" className="gallery-image" />
          </div>
        </Col>
      </Row>
      <Row>
        <Col className={`gallery-section ${galleryInView ? 'slide-in-up' : ''}`}>
          <div className="image-gallery">
            <img src={photo6} alt="Galera 6" className="gallery-image" />
            <img src={photo7} alt="Galera 7" className="gallery-image" />
            <img src={photo8} alt="Galera 8" className="gallery-image" />
            <img src={photo9} alt="Galera 9" className="gallery-image" />
            <img src={photo10} alt="Galera 10" className="gallery-image" />
          </div>
        </Col>
      </Row>
    </Container>
  );
};

export default AboutMe;
